@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    overflow-x: hidden;
    background-color: rgb(232, 232, 232);
}

header {
    font-size: 1.14em;
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: rgb(58,48,31);
    color: rgb(232, 232, 232);
    align-items: center;
    height: 70px;
    box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
}

.navbar {
    display: flex;
    height: 100%;
    margin-left: 2em;
}

.navbar > a {
    display: flex;
    color: inherit;
    list-style: none;
    height: 100%;
    text-decoration: none;
    align-items: center;
    padding-inline: 2em;
}

nav > a:hover {
    background-color:hsl(38, 30%, 15%) ;
}

.container {
    max-width: 1080px;
    text-align: center;
    justify-content: center;
    margin: 1em auto auto;
}

/* Game Page */

.gameCanvas {
    display: block;
    margin: 1em auto auto;
    box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
}

.info {
    display: flex;
    padding: 1em;
    margin: auto;
    justify-content: center;
}

.info > * {
    margin-inline: 1.5em;
}

.controls {
    display: flex;
    justify-content: center;
}

.controls > * {
    margin-inline: 1em;
    padding: 0.5em 1em;
}

.data {
    list-style: none;
    font-size: 1.3em;
    display: flex;
    justify-content: center;
}

.data > * {
    text-transform: capitalize;
    margin-inline: 1em;
    padding: 0.5em 1em;
}

.controls > button {
    cursor: pointer;
    background-color: rgb(116, 139, 47);
    color: rgb(232, 232, 232);
    border: 0;
    font-size: 1em;
}

.controls > button:hover {
    background-color: hsl(75, 47%, 34%);
}

/* Rule Page */


.keys {
    border-top: 1px solid rgb(110, 110, 110);
    padding: 2em 3em;
    margin: auto;
    text-align: left;
    line-height: 2;
}

.rules {
    font-weight: normal;
    line-height: 2;
    font-size: 1.05em;
}

.rules span {
  font-size: 1.1em;
}

.playBtn {
  font-size: 2em;
  color: rgb(116, 139, 47);
}

.playBtn:hover{
  color: hsl(75, 47%, 30%);
}
